import { FORMS_SECURITY_USER_GROUP_ENTITY_TYPE } from "../entities.js";
import type { FormsSecurityUserGroupWorkspaceContext } from "./security-user-group-workspace.context.js";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbWorkspaceContext } from "@umbraco-cms/backoffice/workspace";

export const FORMS_SECURITY_USER_GROUP_WORKSPACE_CONTEXT = new UmbContextToken<
  UmbWorkspaceContext,
  FormsSecurityUserGroupWorkspaceContext
>(
  "UmbWorkspaceContext",
  undefined,
  (context): context is FormsSecurityUserGroupWorkspaceContext =>
    context.getEntityType?.() === FORMS_SECURITY_USER_GROUP_ENTITY_TYPE,
);
